<template>
  <div>
    <center>
      <el-upload
        class="upload-demo"
        action="/audio/info"
        multiple
        :limit="1"
        :on-exceed="handleExceed"
        :file-list="fileList"
        :on-success="onSuccess"
        :before-upload="beforeUpload"
        :on-error="onError"
      >
        <el-button size="small" type="primary">点击上传</el-button>
        <template #tip>
          <div class="el-upload__tip">
            只能上传常见音频格式，系统不存储任何文件，大文件处理会超时。
          </div>
        </template>
      </el-upload>
    </center>
    <br/>
    <div> 
      <el-card>
        <div v-if="!loading" >
          <el-descriptions  v-if="audioInfo" title="包含信息">
              <el-descriptions-item
                v-for="(value, key) in audioInfo"
                :label="key"
                :key="key"
                >{{ value }}</el-descriptions-item
              >
          </el-descriptions>
          <el-empty v-else description="暂无数据"></el-empty>
        </div>
        <div v-else>
          <el-skeleton animated />
        </div>
      </el-card>
    </div>
  </div>
</template>

<script>
export default {
  name: "Picture",
  data() {
    return {
      audioInfo: null,
      loading: false,
    };
  },
  methods: {
    onSuccess(response,f,fileList) {
      this.audioInfo = response.data;
      this.loading = false;
      fileList.pop()
      this.$message.success({
            message: '解析完成。',
            type: 'success'
          })
    },
    onError(){
       this.loading = false;
    },
    beforeUpload(){
      this.loading = true;
    }
  },
};
</script>

